.mc-wrapper {
  background-image: url('./images/firewall@3x.png'),
    url('./images/background_repeat@3x.jpg');
  background-position: bottom, top;
  background-repeat: no-repeat, repeat;
  background-size: 100%;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  .headerbackground {
    width: 100%;
  }
}

// 237 × 58

$header-width: calc(100% - 58px * 2);
$header-height: calc($header-width * 58 / 237);

.popup-background {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 3vh;
  margin-top: -5vh;
  align-self: stretch;

  > .content {
    margin: 50px 20px 20px 20px;
    border-radius: 20px;
    box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.5);
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    position: relative;
    align-self: stretch;
    > .popup-header {
      .header-image {
        width: 237px;
        object-fit: contain;
      }
      margin-top: -30px;
      align-items: center;
      justify-content: center;
      display: flex;

      > .title {
        position: absolute;
        font-size: 20px;
        text-align: center;
        color: #ffffff;

        font-weight: bold;
      }
    }
    > .popup-header-finish {
      margin-top: -35%;
      // margin-bottom: -35%;
      .header-image {
        width: 100%;
        object-fit: contain;
        // background-color: aqua;
      }
    }
  }
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_inner {
  margin: 20px 20px 20px 20px;
  background: white;
  border-radius: 10px;
  overflow: hidden;

  > .header {
    width: 100%;
    height: 50px;
    background-image: linear-gradient(to bottom, #ffc800, #ff9e00);
    align-items: center;
    justify-content: center;
    display: flex;
    position: relative;
    > .title {
      height: 24px;
      font-size: 20px;
      text-align: center;
      color: #ffffff;
    }
    .icclose_page_black {
      position: absolute;
      width: 40px;
      height: 40px;
      left: 4px;
      > .image {
        width: 18px;
        height: 18px;
      }
    }
  }
  .content {
    max-height: 60vh;
    overflow: auto;
    padding: 23px 12px 23px 12px;
    .normal {
      font-size: 16px;
      line-height: 1.25;
      color: #101010;
      br {
        line-height: 1.5;
      }
    }
  }
}
